<template>
  <div class="hello">
    <a @click="getNewBlogList">getNewBlogList</a>
    <a @click="getSomeBlog">getSomeBlog</a>
    <a @click="getCssBlog">getCssBlog</a>
    <section>{{ content }}</section>
  </div>
</template>

<script>
export default {
  name: 'hello',
  data () {
    return {
      content: ''
    }
  },
  methods: {
    getNewBlogList () {
      this.$http
      .get('https://api.github.com/repos/targeral/front-end-Summary/contents')
      .then((res) => {
        this.content = res;
      })
    },
    getSomeBlog () {
    },
    getCssBlog () {

    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}

a {
  color: #42b983;
}
</style>



